import React from "react";

export default function Header({ onOpenContact = () => {} }) {
  const linkBase =
    "px-3 py-2 text-sm font-medium text-white/90 hover:text-white transition";

  return (
    <header className="fixed top-0 left-0 z-50 w-full bg-blue-600/95 backdrop-blur shadow-md">
      <div className="mx-auto flex max-w-7xl items-center justify-between px-6 py-3">
        {/* Logo */}
        <a
          href={`${process.env.PUBLIC_URL}/`}
          className="text-2xl font-extrabold tracking-wide text-white hover:opacity-90 transition"
        >
          HOAN PHI CRYPTO
        </a>

        {/* Menu */}
        <nav className="hidden items-center gap-6 md:flex">
          <a
            href={`${process.env.PUBLIC_URL}/`}
            className={linkBase}
          >
            Trang Ch
          </a>

          <a
            href={`${process.env.PUBLIC_URL}/services`}
            className={linkBase}
          >
            Sn Giao Dch
          </a>

          <a
            href={`${process.env.PUBLIC_URL}/#news`}
            className={linkBase}
          >
            Tin Tc
          </a>

          {/* Nt Lin h m modal */}
          <button
            onClick={onOpenContact}
            className="inline-flex items-center rounded-lg bg-white/10 px-4 py-2 text-sm font-semibold text-white ring-1 ring-white/15 hover:bg-white/20 transition"
          >
            Lin h
          </button>
        </nav>

        {/* Nt menu mobile */}
        <div className="md:hidden">
          <button
            className="text-white/90 hover:text-white transition"
            aria-label="Open menu"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-8 w-8"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M4 6h16M4 12h16M4 18h16"
              />
            </svg>
          </button>
        </div>
      </div>
    </header>
  );
}
