import React from "react";
import NewsCard from "./NewsCard";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Autoplay } from "swiper/modules";
import newsImg from '../assets/anhtintuc.png';
import newsATX from '../assets/atx.png';
// Import css ca Swiper
import "swiper/css";
import "swiper/css/pagination";

const newsList = [
  {
    id: 1,
    title: "HON 50% PH GIAO DCH SN BINGX ",
    description: "Ch cn 1 bc n gin, bn c th nhn li 50% ph giao dch.",
    imgSrc: newsImg,
  },
      
  {
    id: 2,
    title: "HON PH GIAO DCH ",
    description:
      "ng k qua link ca chng ti  nhn li ph giao dch nhanh chng trong 24h.",
    imgSrc:
      "https://w.ladicdn.com/s700x550/67dbe0216346bb0012ce3b79/sukien2-20250501170052-d4azq.png",
  },
  {
    id: 3,
    title: "TNG HP CC SN H TR HON PH",
    description: "Danh sch cc sn ang h tr hon ph giao dch.",
    imgSrc:
      "https://w.ladicdn.com/s700x550/67dbe0216346bb0012ce3b79/sukien3-20250501173033-wwcal.png",
  },
  {
    id: 4,
    title: "CP VN 1M2 VOUCHER ATX",
    description: "H tr voucher tri nghim ti Atx",
    imgSrc:
      newsATX,
  },
  
];

const News = () => (
  <section id="news" className="py-16 text-primary animate-slideIn">
    <h2 className="text-2xl text-center mb-3 text-accent uppercase tracking-wide">
      TIN TC
    </h2>
    <h2 className="text-4xl font-bold text-center mb-12">
      CC BI VIT MI NHT
    </h2>

    <div className="max-w-6xl mx-auto px-6">
      <Swiper
        modules={[Pagination, Autoplay]}
        spaceBetween={30}
        slidesPerView={1}
        loop={true}
        pagination={{
          clickable: true,
          el: ".custom-pagination", //  pagination s render  div ngoi
        }}
        autoplay={{ delay: 3000, disableOnInteraction: false }}
        breakpoints={{
          640: { slidesPerView: 1 },
          768: { slidesPerView: 2 },
          1024: { slidesPerView: 3 },
          1280: { slidesPerView: 4 },
        }}
      >
        {newsList.map((item) => (
          <SwiperSlide key={item.id}>
            <NewsCard {...item} />
          </SwiperSlide>
        ))}
      </Swiper>

      {/* Pagination ring bn ngoi Swiper */}
      <div className="custom-pagination mt-10 flex justify-center"></div>
    </div>

    {/* CSS pagination custom */}
    <style jsx>{`
      .custom-pagination .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
        background: #d1d5db; /* xm nht */
        opacity: 1;
        margin: 0 6px;
      }
      .custom-pagination .swiper-pagination-bullet-active {
        background: #2563eb; /* xanh dng */
        width: 22px; /* di hn khi active */
        border-radius: 8px; /* bo trn thnh thanh ngang */
        transition: all 0.3s ease;
      }
    `}</style>
  </section>
);

export default News;
